:root {
  --active-color: #00acdc;
  --font: Roboto, "Times New Roman", Times, serif;
}

body {
  font-family: var(--font);
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__link {
  width: 40px;
  height: 40px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__list {
  width: 100%;
  height: 60px;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-content: center;
  block-size: 20px;
  padding-top: 23px;
  padding-bottom: 22px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}

.is-active:after {
  background-color: var(--active-color);
  content: "";
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 5px;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.91322885.ttf");
}
